.nav {
  flex: 0 0 194px;
  height: 100%;
  background-color: rgba(204, 204, 204, 0.1);

  &_hl {
    position: relative;

    &:after {
      content: '';
      position: absolute;
      bottom: -300px;
      width: 100%;
      height: 300px;
      background-color: rgba(204, 204, 204, 0.1);
    }

  }

  &.toggled {
    display: block;
  }

}

.nav-menu {
  margin: 0;
  padding: 141px 32px 0;
  list-style: none;

  &__item {
    margin-bottom: 67px;

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__link {
    font-family: $font-family;
    font-size: 14px;
    font-weight: 400;
    font-style: normal;
    font-stretch: normal;
    color: #000000;
    line-height: 1.14;
    letter-spacing: normal;
    text-align: left;
    text-transform: uppercase;
    text-decoration: none;
    transition: color .2s ease-in-out;

    &:hover {
      color: #f5a623;
    }

    &_active {
      color: #f5a623;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .nav {
    display: none;
    position: absolute;
    width: 194px;
    height: 100%;
    left: 4px;
    top: 0;
    background: rgba(240, 236, 236, 0.98);
    z-index: 9;

    &_hl {

      &:after {
        content: none;
      }

    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .nav {
    position: relative;
    display: none;
    background: #fff;

    &_hl {

      &:after {
        content: none;
      }

    }

  }

  .nav-menu {
    padding: 50px 15px 50px;

    &__item {
      margin-bottom: 67px;

      &:last-child {
        margin-bottom: 0;
      }

    }

    &__link {
      font-family: $font-family;
      font-size: 14px;
      font-weight: 400;
      font-style: normal;
      font-stretch: normal;
      color: #000000;
      line-height: 1.14;
      letter-spacing: normal;
      text-align: left;
      text-transform: uppercase;
      text-decoration: none;
      transition: color .2s ease-in-out;

      &:hover {
        color: #f5a623;
      }

      &_active {
        color: #f5a623;
      }

    }

  }


}

