.news {
  padding: 50px 0 50px 35px;
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;

  &__cl {

  }

  &__cl-wrap {
    width: 297px;
    padding-left: 10px;
    background: #fff;
  }

  &__cr {
    flex: 1 1 auto;
  }

}

.news-list-title {
  font-family: $font-family;
  font-size: 48px;
  font-weight: 700;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.08;
  letter-spacing: 2px;
  text-align: left;
  color: #000000;
  text-transform: uppercase;
}

.news-list {
  margin: 0;
  padding: 0;
  list-style: none;

  &__item {
    padding: 37px 0;
    border-bottom: 1px solid #eeeeee;

    &:last-child {
      padding: 26px 0;
      border-bottom: none;
    }

  }

  &__link {
    display: inline-block;
    font-family: $font-family;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.29;
    letter-spacing: normal;
    text-align: left;
    color: #000000;
    text-decoration: none;

    -webkit-background-clip: text;
    background-clip: text;

    transition: background-image .2s ease-in-out;

    &:hover {
      -webkit-text-fill-color: transparent;
      background-image: linear-gradient(84deg, #d20b54, #ffb84a);
      color: #000000;
    }

  }

  &__link-all {
    padding-left: 2px;
    font-family: $font-family;
    font-size: 12px;
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 2.4px;
    color: #808080;
    text-transform: uppercase;
    text-decoration: none;
    transition: color .2s ease-in-out;

    &:hover {
      color: #f5a623;
    }

  }

}

.news-content {
  padding-left: 20px;
  font-family: $font-family;

  & > h1 {
    margin: 0;
    font-size: 20px;
    font-weight: 700;
    font-style: normal;
    //font-stretch: normal;
    line-height: 1.4;
    letter-spacing: normal;
    color: #000000;
    text-transform: uppercase;
  }

  & > h2 {
    margin-top: 30px;
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.4;
    letter-spacing: normal;
    color: #000000;
  }

  & > p {
    margin-top: 30px;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.71;
    letter-spacing: normal;
    color: #000000;
  }

  & > ul {
    margin: 30px 0 0 0;
    padding: 0 0 0 20px;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.71;
    letter-spacing: normal;
    color: #000000;
  }

  & img {
    width: 100%;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .news {
    padding: 50px 0 50px 25px;

    &__cl-wrap {
      width: 240px;
      padding-left: 10px;
      background: #fff;
    }

  }

  .news-list-title {
    font-size: 36px;
    line-height: 1.44;
    letter-spacing: 1.5px;
  }

  .news-content {
    padding-left: 20px;
    padding-right: 20px;
  }

}

