.social {
  display: flex;
  margin: 0;
  list-style: none;

  &__item {
    margin-right: 32px;

    &:last-child {
      margin-right: 0;
    }

  }

  &__item-link {
    display: inline-block;
    transition: opacity .2s ease-in-out;

    &:hover {
      opacity: .7;
    }

  }

  &__item-img {
    vertical-align: top;
  }

}
