.partner {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 15px 0 0 35px;
  list-style: none;

  &_nav {
    display: none;
  }

  &__item {
    margin-right: 55px;

    &:last-child {
      margin-right: 0;
    }

  }

  &__item-link {
    display: inline-block;
  }

  &__item-img {
    vertical-align: top;

    &_1 {
      width: 67px;
      height: 76px;
    }

    &_2 {
      width: 134px;
      height: 51px;
    }

    &_3 {
      width: 153px;
      height: 20px;
    }

    &_4 {
      width: 120px;
      height: 41px;
    }

  }

}


/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .partner {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 15px 0 0 35px;
    list-style: none;

    &_nav {
      display: none;
    }

    &__item {
      margin-right: 30px;

      &:last-child {
        margin-right: 0;
      }

    }

    &__item-link {
      display: inline-block;
    }

    &__item-img {
      vertical-align: top;

      &_1 {
        width: 57px;
        height: 62px;
      }

      &_2 {
        width: 134px;
        height: 51px;
      }

      &_3 {
        width: 128px;
        height: 16px;
      }

      &_4 {
        width: 101px;
        height: 34px;
      }

    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .partner {
    //display: flex;
    align-items: normal;
    flex-direction: column;
    margin: 0;
    padding: 20px 0 0 15px;
    list-style: none;

    &_nav {
      display: flex;
    }

    &__item {
      margin-right: 0;
      margin-bottom: 30px;

      &:last-child {
        margin-right: 0;
        //margin-bottom: 30px;
      }

    }

    &__item-link {
      display: inline-block;
    }

    &__item-img {
      vertical-align: top;

      &_1 {
        width: 67px;
        height: 76px;
      }

      &_2 {
        width: 134px;
        height: 51px;
      }

      &_3 {
        width: 153px;
        height: 20px;
      }

      &_4 {
        width: 120px;
        height: 41px;
      }

    }

  }


}

