.footer-wrap {
  position: relative;
  z-index: 1;
  padding: 34px 0 29px;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__cr {
    padding-left: 36px;
    opacity: 0.45;
    font-family: $font-family;
    font-size: 13px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #3c3d41;
  }

  &__nav {

  }

  &__social {

  }

}

.footer-menu {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;

  &__item {
    margin-left: 46px;

    &:last-child {
      margin-left: 65px;
    }

  }

  &__link {
    font-family: $font-family;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.14;
    letter-spacing: normal;
    text-align: left;
    color: #000000;
    text-transform: uppercase;
    text-decoration: none;
    transition: color .2s ease-in-out;

    &:hover {
      color: #f5a623;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .footer-wrap {
    padding: 34px 20px 29px;
  }

  .footer {
    flex-wrap: wrap;
    align-items: normal;
    justify-content: space-between;

    &__cr {
      width: 50%;
      order: 2;
      padding-left: 0;
      font-size: 12px;
    }

    &__nav {
      width: 100%;
      order: 1;
    }

    &__social {
      width: 50%;
      order: 3;
      display: flex;
      justify-content: flex-end;
    }

  }


  .footer-menu {
    flex-wrap: wrap;
    padding-bottom: 30px;

    &__item {
      margin-left: 46px;

      &:first-child {
        margin-left: 0;
      }

      &:last-child {
        margin-left: auto;
      }

    }

    &__link {

    }

  }


}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .footer-wrap {
    padding: 20px 0;
  }

  .footer {
    flex-wrap: wrap;
    align-items: normal;
    justify-content: space-between;

    &__cr {
      width: 50%;
      order: 2;
      padding-left: 0;
      font-size: 12px;
      }

    &__nav {
      width: 100%;
      order: 1;
    }

    &__social {
      width: 50%;
      order: 3;
      display: flex;
      justify-content: flex-end;
    }

  }

  .footer-menu {
    flex-wrap: wrap;

    &__item {
      width: 50%;
      margin-left: 0;
      margin-bottom: 20px;

      &:last-child {
        margin-left: 0;
        margin-bottom: 30px;
      }

      &:nth-child(2) {
        text-align: right;
      }

      &:nth-child(4) {
        text-align: right;
      }

    }

    &__link {

    }

  }

}

