.bg {
  position: absolute;
  top: 0;
  width: 100%;
  height: 818px;
  overflow: hidden;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 1217px;
    height: 818px;
    background-image: url("../images/banner/3112.png");
    background-repeat: no-repeat;
    background-size: 1217px 818px;
  }

}

.bg-grad {
  display: flex;
  position: relative;
  width: 100%;
  padding-top: 100px;
  clip-path: polygon(0 20%, 100% 0, 100% 100%, 0% 100%);
  background-image: linear-gradient(173deg, #f3f3f3, #ffffff);
  z-index: 3;
}

.bg-body {
  position: relative;

  &:after {
    content: '';
    position: absolute;
    top: 50%;
    width: 100%;
    height: 50%;
    clip-path: polygon(0 20%, 100% 0, 100% 100%, 0% 100%);
    background-image: linear-gradient(173deg, #f3f3f3, #ffffff);
  }

}

@media only screen and (max-width : 1900px) {

  .bg {

    &:after {
      right: -100px;
    }

  }

}

@media only screen and (max-width : 1700px) {

  .bg {

    &:after {
      right: -220px;
    }

  }

}

@media only screen and (max-width : 1700px) {

  .bg {

    &:after {
      right: -300px;
    }

  }

}


/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .bg {
    position: absolute;
    top: 0;
    width: 100%;
    height: 900px;
    overflow: hidden;

    &:after {
      content: '';
      position: absolute;
      top: 240px;
      right: -250px;
      width: 870px;
      height: 585px;
      background-image: url("../images/banner/3112.png");
      background-repeat: no-repeat;
      background-size: 870px 585px;
    }

  }

  .bg-grad {
    padding-top: 70px;
    clip-path: polygon(0 10%, 100% 3%, 100% 100%, 0% 100%);
    background-image: linear-gradient(173deg, #f3f3f3, #ffffff);
    z-index: 3;
  }


}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .bg {
    display: none;
  }

  .bg-grad {
    padding-top: 30px;
    clip-path: polygon(0 3%, 100% 0%, 100% 100%, 0% 100%);
    background-image: linear-gradient(173deg, #f3f3f3, #ffffff);
    z-index: 3;
  }

}

