.content-wrap {
  position: relative;
  width: 100%;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-flow: column nowrap;
  height: 100%;
  min-height: 100vh;
}

.content {
  padding: 50px 0 50px 35px;

  &_mw {
    max-width: 80%;
  }

  &_fs-lg {

    & > p {
      font-size: 18px!important;
    }

    & > ul {
      font-size: 18px!important;
      ine-height: 1.56;
    }

  }

  & > h1 {
    margin: 0;
    font-family: $font-family;
    font-size: 48px;
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.08;
    letter-spacing: 2px;
    text-align: left;
    text-transform: uppercase;
    color: #000000;
  }

  & > h2 {
    margin: 0;
    font-family: $font-family;
    font-size: 36px;
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: 3.6px;
    text-align: left;
    color: #000000;
    text-transform: uppercase;
  }

  & > h3 {
    font-weight: 700;
    font-size: 18px;
    color: #000000;
    line-height: 1.69;
    text-transform: uppercase;
  }

  & > p {
    margin: 45px 0 0 0;
    font-family: $font-family;
    font-size: 16px;
    font-weight: 400;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.69;
    letter-spacing: normal;
    text-align: left;
    color: #000000;
  }

  & > ul {
    margin: 0;
    padding-left: 20px;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.69;
  }

}


/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .content {

    &_mw {
      max-width: 80%;
    }

    &_fs-lg {

      & > p {
        font-size: 18px!important;
      }

      & > ul {
        font-size: 18px!important;
        ine-height: 1.56;
      }

    }

    & > h1 {
      margin: 0;
      font-family: $font-family;
      font-size: 36px;
      font-weight: 700;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.44;
      letter-spacing: 1.5px;
      text-align: left;
      text-transform: uppercase;
      color: #000000;
    }

    & > h2 {
      margin: 0;
      font-family: $font-family;
      font-size: 36px;
      font-weight: 700;
      font-style: normal;
      font-stretch: normal;
      letter-spacing: 3.6px;
      text-align: left;
      color: #000000;
      text-transform: uppercase;
    }

    & > h3 {
      font-weight: 700;
      font-size: 18px;
      color: #000000;
      line-height: 1.69;
      text-transform: uppercase;
    }

    & > p {
      margin: 40px 0 0 0;
      font-family: $font-family;
      font-size: 16px;
      font-weight: 400;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.69;
      letter-spacing: normal;
      text-align: left;
      color: #000000;
    }

    & > ul {
      margin: 0;
      padding-left: 20px;
      font-size: 16px;
      font-weight: 400;
      line-height: 1.69;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .content {
    padding: 30px 0;

    &_mw {
      max-width: 100%;
    }

    &_fs-lg {

      & > p {
        font-size: 14px!important;
        line-height: 1.64;
      }

      & > ul {
        font-size: 14px!important;
        line-height: 1.64;
      }

    }

    & h1 {
      font-size: 24px;
      line-height: 1.46;
      letter-spacing: 1.2px;
    }

    & h2 {
      font-size: 24px;
      line-height: 1.46;
      letter-spacing: 1.2px;
    }

    & h3 {
      font-size: 14px;
      line-height: 1.64;
      letter-spacing: normal;
    }

    & > p {
      font-size: 14px;
      line-height: 1.64;
    }

  }

}

