.contact-list {
  display: flex;
  flex-wrap: wrap;
  padding-top: 70px;
  margin: 0 -18px;

  &__item {
    width: 33.33%;
    padding: 0 18px 36px;
  }

}

.contact {
  padding: 25px;
  border-radius: 8px;
  background-color: #ffffff;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.13);
}

.contact-head {
  display: flex;
  align-items: center;
  padding-bottom: 20px;
  border-bottom: 1px solid #e4ebf0;

  &__img-wrap {
    min-width: 42px;
    max-width: 42px;
  }

  &__img {
    width: 100%;
    height: auto;
    vertical-align: top;
    border-radius: 50px;
  }

  &__info-wrap {
    flex: 1 1 auto;
    padding-left: 18px;
  }

  &__info {

    & > h2 {
      margin: 0;
      opacity: 0.7;
      font-family: $font-family;
      font-size: 16px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.75;
      letter-spacing: normal;
      color: #000000;
    }

    & > p {
      margin: 0;
      font-family: $font-family;
      font-size: 14px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.57;
      letter-spacing: normal;
      color: #b3b3b3;
    }

  }

}

.contact-body {
  padding-top: 20px;

  &__phone {
    margin: 0;
    font-family: $font-family;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: normal;
    color: #000;
    line-height: 1.86;
    text-decoration: none;
  }

  &__email {
    margin: 0;
    font-family: $font-family;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: normal;
    color: #f5a623;
    line-height: 1.86;
    text-decoration: none;
    word-wrap: break-word;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .contact-list {
    margin: 0 0 0 -18px;

    &__item {
      width: 50%;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .contact-list {
    margin: 0;
    padding-top: 40px;

    &__item {
      width: 100%;
      padding: 0 0 36px;
    }

  }

}

