.container {
  position: relative;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;

  &_main {
    display: flex;
    //flex: 1;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .container {
    padding: 0 4px;
  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .container {
    padding: 0 15px;

    &_main {
      flex-direction: column;
    }

    &__order-1 {
      order: 1;
    }

    &__order-2 {
      order: 2;
    }

  }

}

