.header {
  position: relative;
  z-index: 1;

  &__wrap {
    display: flex;
  }

  &__logo {
    flex: 0 0 194px;
    padding: 18px 0 0 32px;
    background-color: rgba(204, 204, 204, 0.1);
  }

  &__partner {
    flex-grow: 1;
  }

  &__social {
    padding: 37px 0 0 0;
  }

  &__menu-btn {
    display: none;
    position: absolute;
    padding: 0;
    top: 43px;
    left: 40px;
    width: 28px;
    height: 28px;
    vertical-align: top;
    background: transparent;
    //background: url("../images/menu.svg") no-repeat;
    //background-size: 50px 50px;
    border: 0;
    outline: none;
    stroke: #6b6f81!important;
    cursor: pointer;

    &.toggled {
      stroke: #f5a623!important;
    }

  }

  &__menu-btn-img {
    width: 28px;
    height: 28px;
  }

}


/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .header {
    position: relative;
    z-index: 1;

    &__wrap {
      display: flex;
    }

    &__logo {
      flex: 0 0 194px;
      padding: 18px 0 0 32px;
      background-color: rgba(204, 204, 204, 0.1);

      &.toggled {
        background: rgba(240, 236, 236, 0.98);
      }

    }

    &__partner {
      flex-grow: 1;
    }

    &__social {
      display: none;
    }

    &__menu-btn {
      display: inline-block;
      position: absolute;
      padding: 0;
      top: 43px;
      left: 40px;
      width: 28px;
      height: 28px;
      vertical-align: top;
      background: transparent;
      border: 0;
      outline: none;
      stroke: #6b6f81!important;
      cursor: pointer;

      &.toggled {
        stroke: #f5a623!important;
      }

    }

    &__menu-btn-img {
      width: 28px;
      height: 28px;
    }

  }


}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .header {

    &__wrap {
      justify-content: center;
    }

    &__logo {
      flex: 0 0 117px;
      padding: 14px 0;
      background-color: #fff;

      &.toggled {
        background-color: #fff;
      }

    }

    &__partner {
      display: none;
    }

    &__social {
      display: none;
    }

    &__menu-btn {
      display: inline-block;
    }

  }


}

